<template>
  <view class="uv-page">
    <uv-form
      :labelPosition="formLabelPosition"
      :labelAlign="formLabelAlign"
      :labelStyle="formLabelStyle"
      :labelWidth="formLabelWidth"
      :errorStyle="errorStyle"
      :errorType="errorType"
      :model="baseModel"
      :ref="formRef"
    >
      <uv-form-item
        v-for="item in optionList"
        :key="item.prop"
        :label="item.label"
        :prop="item.prop"
        :required="item.required"
        :labelPosition="item.labelPosition ? item.labelPosition : formLabelAlign"
        :borderBottom="getBorderBottom(item)"
        :customStyle="formItemCustomStyle"
        :labelWidth="formItemLabelWidth"
      >
        <!-- input 文本输入 -->
        <uv-input
          v-if="item.type == 'input'"
          v-model="baseModel[item.prop]"
          :inputAlign="inputAlign"
          border="none"
          :placeholder="item.placeholder"
        ></uv-input>

        <!-- radio  -->
        <uv-radio-group
          v-if="item.type == 'radio'"
          v-model="baseModel[item.prop]"
          :customStyle="radioGroupCustomStyle"
        >
          <uv-radio
            :customStyle="{ marginRight: '16px' }"
            v-for="(_item, _index) in item.options"
            :key="_index"
            :label="_item.label"
            :name="_item.value"
          ></uv-radio>
        </uv-radio-group>

        <!-- checkbox  -->
        <uv-checkbox-group
          v-if="item.type == 'checkbox'"
          v-model="baseModel[item.prop]"
          shape="square"
          :customStyle="radioGroupCustomStyle"
        >
          <uv-checkbox
            :customStyle="{ marginRight: '16px' }"
            v-for="(_item, _index) in item.options"
            :key="_index"
            :label="_item.label"
            :name="_item.value"
          ></uv-checkbox>
        </uv-checkbox-group>

        <!-- textarea -->
        <uv-textarea
          v-if="item.type == 'textarea'"
          :placeholder="item.placeholde"
          v-model="baseModel[item.prop]"
          count
        ></uv-textarea>

        <!-- picker -->
        <view
          v-if="item.type == 'picker'"
          @click.native="showPickerSelect(item)"
          style="width: 100%"
        >
          <uv-input
            v-model="baseModel[item.strProp ? item.strProp : item.prop]"
            disabled
            disabledColor="#ffffff"
            :inputAlign="inputAlign"
            :placeholder="item.placeholder"
            border="none"
          ></uv-input>
        </view>

        <template v-if="item.type == 'picker'" v-slot:right>
          <uv-icon name="arrow-right" @click="showPickerSelect(item)"></uv-icon>
        </template>
        <uv-picker
          v-if="item.type == 'picker'"
          :ref="'picker.' + item.prop"
          :columns="item.options"
          keyName="label"
          @confirm="(e) => pickerSelectConfirm(e, item)"
          @change="(e) => pickerSelectChange(e, item)"
          @close="closePopUp"
        ></uv-picker>

        <!-- city -->
        <view
          v-if="item.type == 'city'"
          @click.native="showCityPickerSelect(item)"
          style="width: 100%"
        >
          <uv-input
            v-model="baseModel[item.strProp ? item.strProp : item.prop]"
            disabled
            disabledColor="#ffffff"
            :inputAlign="inputAlign"
            :placeholder="item.placeholder"
            border="none"
          ></uv-input>
        </view>

        <template v-if="item.type == 'city'" v-slot:right>
          <uv-icon name="arrow-right" @click="showCityPickerSelect(item)"></uv-icon>
        </template>
        <city-picker
          v-if="item.type == 'city'"
          :ref="'city.' + item.prop"
          @confirm="(e) => citySelectConfirm(e, item)"
          @close="closePopUp"
        ></city-picker>

        <!-- multiplePicker -->
        <view
          v-if="item.type == 'multiple'"
          @click.native="showMultiplePickerSelect(item)"
          style="width: 100%"
        >
          <uv-input
            v-model="baseModel[item.strProp ? item.strProp : item.prop]"
            disabled
            disabledColor="#ffffff"
            :inputAlign="inputAlign"
            :placeholder="item.placeholder"
            border="none"
          ></uv-input>
        </view>

        <template v-if="item.type == 'multiple'" v-slot:right>
          <uv-icon name="arrow-right" @click="showMultiplePickerSelect(item)"></uv-icon>
        </template>
        <multiple-picker
          v-if="item.type == 'multiple'"
          :ref="'multiple.' + item.prop"
          :columns="item.options"
          :default-index="baseModel[item.prop]"
          @confirm="(e) => multiplePickerConfirm(e, item)"
          @cancel="closePopUp"
        ></multiple-picker>

        <!-- 时间选择 -->
        <view v-if="item.type == 'time'" @click.native="showTimeSelect(item)">
          <uv-input
            v-model="baseModel[item.prop]"
            disabled
            disabledColor="#ffffff"
            :inputAlign="inputAlign"
            :placeholder="item.placeholder"
            border="none"
          ></uv-input>
        </view>
        <template v-if="item.type == 'time'" v-slot:right>
          <uv-icon name="arrow-right" @click="showTimeSelect(item)"></uv-icon>
        </template>
        <uv-datetime-picker
          v-if="item.type == 'time'"
          :ref="'time.' + item.prop"
          mode="time"
          @confirm="(e) => timeSelectConfirm(e, item)"
          @close="closePopUp"
        ></uv-datetime-picker>

        <!-- 日期选择 -->
        <view v-if="item.type == 'date'" @click.native="showDateSelect(item)" style="width: 100%">
          <uv-input
            v-model="baseModel[item.prop]"
            disabled
            disabledColor="#ffffff"
            :inputAlign="inputAlign"
            :placeholder="item.placeholder"
            border="none"
          ></uv-input>
        </view>
        <template v-if="item.type == 'date'" v-slot:right>
          <uv-icon name="arrow-right" @click="showDateSelect(item)"></uv-icon>
        </template>
        <view v-if="item.type == 'date'">
          <uv-datetime-picker
            :ref="'date.' + item.prop"
            mode="date"
            :minDate="0"
            @confirm="(e) => dateSelectConfirm(e, item)"
            @close="closePopUp"
          ></uv-datetime-picker>
        </view>

        <!-- 年选择 -->
        <view v-if="item.type == 'year'" @click.native="showYearSelect(item)" style="width: 100%">
          <uv-input
            v-model="baseModel[item.prop]"
            disabled
            disabledColor="#ffffff"
            :inputAlign="inputAlign"
            :placeholder="item.placeholder"
            border="none"
          ></uv-input>
        </view>
        <template v-if="item.type == 'year'" v-slot:right>
          <uv-icon name="arrow-right" @click="showYearSelect(item)"></uv-icon>
        </template>
        <view v-if="item.type == 'year'">
          <uv-datetime-picker
            :ref="'year.' + item.prop"
            mode="year"
            :minDate="2"
            @confirm="(e) => dateSelectConfirm(e, item)"
            @close="closePopUp"
          ></uv-datetime-picker>
        </view>

        <!-- 图片上传 -->
        <view v-if="item.type == 'image'">
          <ImgUpload
            :previewFullImage="true"
            :maxCount="item.maxCount ? item.maxCount : 1"
            @getFileList="(e) => getFileList(e, item)"
            :fileList="baseModel[item.prop]"
            :customStyle="item.customStyle ? item.customStyle : {}"
          ></ImgUpload>
        </view>

        <!-- 图片上传 -->
        <view
          v-if="item.type == 'uploadImage'"
          @click="changeImageUpload(item)"
          class="upload-image-box"
        >
          <text v-if="!baseModel[item.prop]">请选择</text>
          <image v-else :src="baseModel[item.prop]" class="upload-image" mode="aspectFill" />
        </view>
        <template v-if="item.type == 'uploadImage'" v-slot:right>
          <uv-icon name="arrow-right" @click="changeImageUpload(item)"></uv-icon>
        </template>
      </uv-form-item>
    </uv-form>
  </view>
</template>

<script>
import ImgUpload from '@/components/upload/imgs.vue'
import cityPicker from '@/components/city-picker/index.vue'
import multiplePicker from '@/components/multiple-picker/multiple-picker.vue'
import { uploadPic } from '@/api/base/index.js'
import props from './paFormProps.js'
import { formatTimeBase } from '@/utils/timeUtils.ts'
export default {
  mixins: [props],
  components: {
    ImgUpload,
    cityPicker,
    multiplePicker,
  },
  data() {
    return {
      baseModel: {},
      formRef: 'ref' + new Date().getTime(),
    }
  },
  watch: {
    value: {
      handler(val) {
        this.baseModel = val
      },
      immediate: true,
    },
    rules: {
      handler(val) {
        if (val && this.$refs[this.formRef] && Object.keys(this.baseModel).length) {
          this.$refs[this.formRef].setRules(this.rules)
        }
      },
    },
  },
  created() {},
  methods: {
    getBorderBottom(item) {
      if (Object.hasOwn(item, 'borderBottom')) {
        return item.borderBottom
      }
      return this.borderBottom
    },
    closePopUp() {
      uni.$emit('SHOW_POPUP', { flag: false })
    },
    // picker选择器
    showPickerSelect(item) {
      let refKey = 'picker.' + item.prop
      console.log(this.$refs)
      this.$refs[refKey][0].open()
      uni.$emit('SHOW_POPUP', { flag: true })
      this.hideKeyboard()
    },
    pickerSelectConfirm(e, item) {
      console.log('------pickerSelectConfirm------', { e, item })
      this.baseModel[item.prop] = String(e.value[0].value)
      if (item.strProp) {
        this.baseModel[item.strProp] = e.value[0].label
      }
      this.$refs[this.formRef].validateField(item.prop)
    },
    pickerSelectChange(e, item) {
      if (item.change instanceof Function) {
        item.change(item)
      }
    },
    // city选择器
    showCityPickerSelect(item) {
      let refKey = 'city.' + item.prop
      this.$refs[refKey][0].open()
      uni.$emit('SHOW_POPUP', { flag: true })
      this.hideKeyboard()
    },
    citySelectConfirm(e, item) {
      console.log('------pickerSelectConfirm------', { e, item })
      let index = e.value.length - 1
      this.baseModel[item.prop] = String(e.value[index].company_area_id)
      if (item.strProp) {
        this.baseModel[item.strProp] = e.value.map((c) => c.name).join(' ')
      }
    },

    showMultiplePickerSelect(item) {
      let refKey = 'multiple.' + item.prop
      this.$refs[refKey][0].open()
      uni.$emit('SHOW_POPUP', { flag: true })
      this.hideKeyboard()
    },
    multiplePickerConfirm(e, item) {
      console.log(e)
      let strList = e.selected.map((c) => {
        return c.label
      })
      this.baseModel[item.prop] = e.value
      if (item.strProp) {
        this.baseModel[item.strProp] = strList.join('/')
      }
    },

    // time选择器
    showTimeSelect(item) {
      let refKey = 'time.' + item.prop
      this.$refs[refKey][0].open()
      uni.$emit('SHOW_POPUP', { flag: true })
      this.hideKeyboard()
    },
    timeSelectConfirm(e, item) {
      console.log('------timeSelectConfirm------', { e, item })
      this.baseModel[item.prop] = e.value
    },
    // 日期选择
    showDateSelect(item) {
      console.log('dateSelect------', { item })
      let refKey = 'date.' + item.prop
      this.$refs[refKey][0].open()
      uni.$emit('SHOW_POPUP', { flag: true })
      this.hideKeyboard()
    },
    // 日期选择
    showYearSelect(item) {
      console.log('yearSelect------', { item })
      let refKey = 'year.' + item.prop
      this.$refs[refKey][0].open()
      uni.$emit('SHOW_POPUP', { flag: true })
      this.hideKeyboard()
    },
    dateSelectConfirm(e, item) {
      console.log('----dateSelectConfirm-----', { e, item })
      this.baseModel[item.prop] = formatTimeBase(e.value, '{y}-{m}-{d}')
    },
    // 图片上传
    getFileList(e, item) {
      console.log('----getFileList-----', { e, item })
      // let list = e.map(c => c.relativeUrl);
      let list = e.map((c) => c.url)
      this.baseModel[item.prop] = list
    },
    async changeImageUpload(item) {
      let file = await this.chooseImage()
      uploadPic({ filePath: file }).then((resp) => {
        let res = JSON.parse(resp.data)
        if (res.code == 200) {
          this.baseModel[item.prop] = res.data.imgUrl
        } else {
          this.$showToastNone(res.message)
        }
      })
    },

    chooseImage() {
      return new Promise((resolve, reject) => {
        uni.chooseImage({
          count: 1, //默认9
          sizeType: ['original', 'compressed'], //可以指定是原图还是压缩图，默认二者都有
          success: (res) => {
            const fileName = res.tempFiles[0].name
            const fileSize = res.tempFiles[0].size
            resolve(res.tempFilePaths[0])
          },
        })
      })
    },
    validate() {
      this.$refs[this.formRef].setRules(this.rules)
      return new Promise((resolve) => {
        this.$refs[this.formRef]
          .validate()
          .then((res) => {
            console.log(12321312)
            resolve(true)
          })
          .catch((errors) => {
            resolve(false)
          })
      })
    },
    getData() {
      return this.baseModel
    },
    getCustomData() {
      let dataCopy = JSON.parse(JSON.stringify(this.baseModel))
      let data = {}
      for (let key in dataCopy) {
        if (!String(key).endsWith('str')) {
          let str = key.replace(/^field_/, '')
          data[str] = dataCopy[key]
        }
      }
      return data
    },
    // 重置
    reset() {
      this.$refs[this.formRef].resetFields()
      this.$refs[this.formRef].clearValidate()
    },
    hideKeyboard() {
      uni.hideKeyboard()
    },
  },
}
</script>

<style lang="scss" scoped>
.uv-page {
  width: 100%;
}
.upload-image-box {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  width: 100%;
  color: rgb(192, 196, 204);
}
.upload-image {
  display: block;
  width: 72rpx;
  height: 72rpx;
  border-radius: 72rpx;
  margin-right: 6rpx;
}
</style>
